import { Component, OnInit, ContentChild, TemplateRef, Output, EventEmitter, Input } from '@angular/core';

@Component({
  selector: 'dec-list-grid',
  template: `<div fxLayout="row wrap" [fxLayoutGap]="itemGap" >
  <div *ngFor="let row of rows; let i = index;" (click)="onItemClick($event, row, rows, i)" [fxFlex]="itemWidth">
    <div [ngStyle]="{margin: itemGap}">
      <ng-container
        [ngTemplateOutlet]="templateRef"
        [ngTemplateOutletContext]="{row: row || {}, list: rows || [], index: i}"
      ></ng-container>
    </div>
  </div>
</div>
`,
  styles: [``]
})
export class DecListGridComponent implements OnInit {

  @ContentChild(TemplateRef) templateRef: TemplateRef<any>;

  @Input() itemWidth = '280px';

  @Input() itemGap = '8px';

  @Output() rowClick: EventEmitter<any> = new EventEmitter<any>();

  private _rows = [];

  constructor() { }

  set rows(v: any) {
    if (this._rows !== v) {
      this._rows = v;
    }
  }

  get rows() {
    return this._rows;
  }

  ngOnInit() {
  }

  onItemClick(event, item, list, index) {

    this.rowClick.emit({event, item, list, index});

  }

}
